$('.js-comfort-input').find('.iqdropdown').iqDropdown({
  setSelectionText: (itemCount, totalItems) => {
    let { bedrooms, beds, bathrooms } = itemCount;
    let result = ' '

    if (totalItems > 0) {
      result = '';

      let bedroomsWord = '';
      if (bedrooms > 4) bedroomsWord = ''
      else if (bedrooms > 1) bedroomsWord = '';
      if (bedrooms > 0) result += bedrooms + ' ' + bedroomsWord + ', ';

      let bedsWord = '';
      if (beds > 4) bedsWord = ''
      else if (beds > 1) bedsWord = '';
      if (beds > 0) result += beds + ' ' + bedsWord + ', ';

      let bathroomsWord = ' ';
      if (bathrooms > 4) bathroomsWord = ' '
      else if (bathrooms > 1) bathroomsWord = ' ';
      if (bathrooms > 0) result += bathrooms + ' ' + bathroomsWord;
      else result = result.slice(0, -2);
    }

    return result;
  },
});